*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

body{
  display: flex;
  height: 100vh;
}

.sidebar{
  width: 20%;
  background-color: #363636;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 2rem .5rem;
}

.sidebar__title{
  margin-bottom: .8rem;
}

.sidebar__description{
  margin-bottom: 2rem;
}

.sidebar__options{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebar__option{
  width: 100%;
  margin-bottom: 1rem;
  outline: none;
  padding: .5rem .2rem;
}

.sidebar__colorInput{
  padding: 0 5rem;
  height: 2rem;
}

.sidebar__dowloadBtn{
  cursor: pointer;
  text-decoration: none;
  color: #000;
  outline: none;
  margin-top: 2rem;
  background-color: #fff;
  border: none;
  padding: .5rem 1.5rem;
}

.mainBox{
  width: 80%;
  background-color: #535353;
  display: flex;
  justify-content: center;
  align-items: center;
}

#canvasBox{
  position: relative;
  width: 500px;
  height: 500px;
  background-color: #fff;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

